* {
  box-sizing: border-box;
}

.dc-ui-gallery-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 16px;
  --_aspect-ratio: initial;

  &--ratio-16-9 {
    --_aspect-ratio: 16 / 9;
  }

  &--ratio-1-1 {
    --_aspect-ratio: 1 / 1;
  }

  .image {
    width: 100%;
    line-height: 0;
    overflow: hidden;
    aspect-ratio: var(--_aspect-ratio);
  }

  .copy {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
}
